body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.deposit {
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: rgba(0, 0, 0, 0.55);
  font-family: Days One;
  color: #fff;
  .deposit_container {
    position: relative;
    border: 2px solid #00ffff;
    padding: 5%;
    border-radius: 24px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
    background-color: #000;
    h2 {
      font-size: 36px;
      line-height: 130%;
      color: #c7ff4f;
    }
    p {
      font-weight: 400;
      font-size: 15px;
      line-height: 18px;
      margin-top: -10px;
    }
    .deposit_input {
      display: flex;
      align-items: center;
      justify-content: space-between;
      max-width: 300px;
      width: 100%;
      height: 60px;
      border: 2px solid #ff00ff;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 14px;
      margin: 10px 0;
      img {
        height: 30px;
        padding: 12px;
        border: 2px solid #ff00ff;
        border-radius: 14px;
        margin-left: -2px;
      }
      div {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
        input {
          height: 45px;
          max-width: 120px;
          font-size: 24px;
          font-family: Days One;
          padding-left: 20px;
          text-align: center;
          background: #000;
          border: none;
          color: #fff;
          outline: none;
        }
        a {
          font-family: Days One;
          cursor: pointer;
          font-size: 16px;
          text-decoration-line: underline;
          color: #00ffff;
          background: #000;
          border: none;
        }
      }
    }
  }
  .btn_secondary {
    font-family: Days One;
    max-width: 260px;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    height: 50px;
    background: #ff00ff;
    border-radius: 12px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
  }
  .popup_container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
    background: #000;
    border: 2px solid #00ffff;
    border-radius: 16px;
    height: 200px;
    min-width: 400px;
    max-width: 558px;
    width: 100%;
    padding: 5%;
  }
  .close {
    position: absolute;
    right: 12px;
    top: 12px;
    height: 35px;
    width: 35px;
    cursor: pointer;
  }
}
